<template>
  <div id="app">
    <VerticalTap :data=data1 style="width:200px;"></VerticalTap>
    <HorizontalTap :data=data2 style="margin-top:20px;"></HorizontalTap>

    选择文件: <input type="file" name="fileUpload" id="fileUp" @change="changeFile($event)" ref="inputFile" >

    <el-calendar></el-calendar>
  </div>
</template>

<script>
import VerticalTap from '../../components/tap/VerticalTap.vue'
import HorizontalTap from '../../components/tap/HorizontalTap.vue'
export default {
  name: 'app',
  components: {
    VerticalTap,
    HorizontalTap
  },
  data () {
    return {
      data1: [{
        name: '个人资料',
        url: '',
        active: true
      }, {
        name: '账户管理',
        url: ''
      }],
      data2: [{
        name: '等级',
        url: ''
      }, {
        name: '编程积分',
        url: '',
        active: true
      }, {
        name: '实战积分',
        url: ''
      }]
    }
  },
  methods: {
    changeFile (event) {
      let file = event.target.files[0]
      let formData = new FormData()
      formData.append('file', file)
      formData.append('teamid', '1')
      formData.append('stepid', '2')
      this.$http.uploadFile('/doproject/output/upload', formData, res => {
        if (res.success) {
          console.log(res.data)
        } else {
          // 返回错误的处理
        }
      })
    }
  }
}
</script>

<style scoped>
  @import "../../assets/css/bootstrap.css";
  @import "../../assets/css/theme.css";
  @import "../../assets/css/text.css";
  #app{
    padding:50px;
    background-color: #F8FAFC;
  }
</style>
